import { Button, Container, Col, Row, Table} from "react-bootstrap";
import { useCart } from "react-use-cart";
import { BsCartCheck, BsCartX } from "react-icons/bs";
import { useThemeHook } from "../../../GlobalComponents/ThemeProvider";
import Header from "../Header/Header";
import { Link } from "@reach/router";
import './Cart.css';


const Cart = () => {
  const [theme] = useThemeHook();
  const {
    isEmpty,
    items,
    cartTotal,
    updateItemQuantity,
    removeItem,
    emptyCart,
  } = useCart();
  return (
    <>
    
    <Container className="">
      <Row className="justify-content-center">
        <Header/>
      </Row>
      <h1
        className={`${
          theme ? "text-light" : "text-light-primary"
        } my-5 text-center`}
      >
        {isEmpty ?<Link style={{textDecoration:'none'}} to="/Home">
        <div style={{height:'100vh'}} className="card mt-5 pt-5">
  <div className="card-body mt-5">
    <p className="animate-charcter card-text">Your Cart is Empty, ADD some products.</p>
    <div>
    <Button style={{outline:'none'}} className="animate-charcter">Back to store</Button>
    </div>
  </div>
</div>
        </Link>  : <Container className="pt-5">Your Cart</Container>}
      </h1>
      <Row className="justify-content-center">
        <Table
          responsive="sm"
          striped
          bordered
          hover
          variant={theme ? "dark" : "light"}
          className="mb-5"
        >
          <tbody>
            {items.map((item, index) => {
              return (
                <tr key={index}>
                  <td>
                    <div
                      style={{
                        background: "white",
                        height: "8rem",
                        overflow: "hidden",
                        display: "flex",
                        justifyContent: "center",
                        alignItems: "center",
                      }}
                    >
                      <div style={{ padding: ".5rem" }}>
                        <img
                          src={item.images}
                          style={{ width: "15rem" }}
                          alt={item.title}
                        />
                      </div>
                    </div>
                  </td>
                  <td>
                    <h6
                      style={{
                        whiteSpace: "nowrap",
                        width: "14rem",
                        overflow: "hidden",
                        textOverFlow: "ellipsis",
                      }}
                    >
                      {item.title}
                    </h6>
                  </td>
                  <td>{item.price} $</td>
                  <td>Quantity ({item.quantity})</td>
                  <td>
                    <Button
                      onClick={() =>
                        updateItemQuantity(item.id, item.quantity - 1)
                      }
                      className="ms-2"
                    >
                      -
                    </Button>
                    <Button
                      onClick={() =>
                        updateItemQuantity(item.id, item.quantity + 1)
                      }
                      className="ms-2"
                    >
                      +
                    </Button>
                    <Button
                      variant="danger"
                      onClick={() => removeItem(item.id)}
                      className="ms-2"
                    >
                      Remove Item
                    </Button>
                  </td>
                </tr>
              );
            })}
          </tbody>
        </Table>
        {!isEmpty && (
          <Row
            style={{ position: "fixed", bottom: 0 }}
            className={`${
              theme ? "bg-light-black text-light" : "bg-light text-balck"
            } justify-content-center w-100`}
          >
            <Col className="py-2">
              <h4>Total Price: {cartTotal} $ </h4>
            </Col>
            <Col className="p-0" md={4}>
              <Button
                variant="danger"
                className="m-2"
                onClick={() => emptyCart()}
              >
                <BsCartX size="1.7rem" />
                Clear Cart
              </Button>
              <Link to="/CheckOut">
              <Button variant="success" className="m-2">
                <BsCartCheck size="1.7rem" />
                Checkout
              </Button>
              </Link>
            </Col>
          </Row>
        )}
      </Row>
    </Container>
    </>
  );
};

export default Cart;









